.room-heading {
  text-decoration: underline;
}
input {
  background-color: white;
  color: blue;
  border: none;
  height: 60px;
  width: calc(100% - 20px);
  padding: 0 10px;
  margin: 0;
}

.q-singer {
  border-bottom: 5px solid white;
}

.search-q,
.flex {
  display: flex;
}
.flex-grow {
  flex-grow: 1;
}
.search-q .list-btn {
  width: 30px;
}
.search-q-input {
  width: calc(100% - 60px);
}

.search-results li {
  word-break: break-word;
}

.search-results .img-container {
  min-width: 120px;
  margin-right: 10px;
}

#search-results-container {
  padding-top: 50px;
}
